<Transition mode="out-in" name="upcoming-2"><div class="main-upcoming" v-if="upcoming"><UpcomingItem v-for="(movie, idx) in upcoming" :key="movie.id" :movie="movie" :idx="idx" :slideView="slideView" :next="upcoming[idx + 1 == upcoming.length ? 0 : idx + 1]" @slideNext="slideNext"></UpcomingItem></div><div class="loading" v-else><div class="loading__spinner"></div></div></Transition>