<div class="main-info" :class="{active: selectedId != null}"><img class="main-info__close" src="@/assets/img/close.svg" alt="alt" @click="$emit('close')"><div class="main-info-block" v-if="selectedMovie"><img class="main-info__img" :src="imgFullUrl + selectedMovie.backdrop_path" alt="alt" v-if="selectedMovie.backdrop_path != null"><img class="main-info__img" src="@/assets/img/no_image.png" alt="alt" v-else><div class="main-info-content"><div class="main-info-content-block"> <h2>{{ selectedMovie.title || selectedMovie.name }}</h2><p class="text">{{ selectedMovie.overview != "" ? selectedMovie.overview : 'Описание пока не придумали)' }}</p><p class="date"> <span>{{ new Date(selectedMovie.release_date).getFullYear() ||  new Date(selectedMovie.last_air_date).getFullYear()}},</span><span v-for="(genre, idx) in selectedMovie.genres" :key="genre.id">{{ genre.name }}<span v-if="idx &lt; selectedMovie.genres.length - 1">, </span></span></p><div class="actors"><Actors :type="page" :id="selectedMovie.id" :count="4"></Actors></div><BtnMore :page="page" :id="selectedMovie.id"></BtnMore></div></div></div></div>