<header class="header"><nav class="header-nav"><div class="container header-nav-content"><router-link to="/"><img src="./../assets/img/logo.svg" alt=""></router-link><button class="burger" @click="burger = !burger"><img src="./../assets/img/bars.svg" alt="alt"></button><ul class="header-nav-content-list" :class="{active: burger}"><li v-for="link in links" :key="link.url"><router-link class="header-nav-content-list__link" :to="link.url">{{ link.title }}</router-link></li></ul></div></nav></header>