<Transition mode="out-in" name="upcoming"><div class="upcoming-item" v-if="slideView == idx"> <img class="upcoming-item__bg" :src="imgBackground + movie.backdrop_path" alt=""><div class="upcoming-item-content"> <h1>{{movie.title}}</h1><p>{{movie.overview}}</p><BtnMore :id="movie.id"></BtnMore></div><div class="upcoming-item-next" @click="$emit('slideNext')"><img :src="imgPoster + next.backdrop_path" alt=""><div class="upcoming-item-next-content"><span class="next">Следующий</span><span class="title">{{next.title}}</span><div class="line"></div></div></div></div></Transition>