<section class="main-media"><router-link class="main-media__title" to="/serials"><span>Сериалы</span><img src="@/assets/img/arrow.svg" alt=""></router-link><Swiper :modules="modules" :space-between="25" navigation :breakpoints="swiperOpt.breakpoints"><SwiperSlide class="main-media-item tvs" v-for="(tv, idx) in popularTvs" :key="tv.id" @click="getTv(tv, idx)"><img :src="imgPoster + tv.poster_path" alt=""></SwiperSlide><SwiperSlide class="main-media-item tvs"><router-link to="/serials"><img src="@/assets/img/arrow.svg" alt=""><span class="all__films">Все сериалы</span></router-link></SwiperSlide></Swiper><InfoBlock :selectedId="selectedId" :selectedMovie="selectedMovie" :index="index" @close="selectedId = null" page="/tv/"></InfoBlock></section>