<section class="main-media"><router-link class="main-media__title" to="/films"><span>Фильмы</span><img src="@/assets/img/arrow.svg" alt=""></router-link><Swiper :modules="modules" :space-between="25" navigation :breakpoints="swiperOpt.breakpoints"><SwiperSlide class="main-media-item" v-for="(movie, idx) in popularMovies" :key="movie.id" @click="getMovie(movie, idx)"><img :src="imgPoster + movie.poster_path" alt=""></SwiperSlide><SwiperSlide class="main-media-item"><router-link to="/films"><img src="@/assets/img/arrow.svg" alt=""><span class="all__films">Все фильмы</span></router-link></SwiperSlide></Swiper><InfoBlock :selectedId="selectedId" :selectedMovie="selectedMovie" :index="index" @close="selectedId = null" page="/movie/"></InfoBlock></section>